import React from "react";
import { Form, Input, Button, Checkbox, Row } from "antd";
import Title from "antd/lib/typography/Title";
import { AccountRepository } from "../../../repository/Account";
import { Spin } from "antd";

// const layout = {
//   labelCol: {
//     span: 8,
//   },
//   wrapperCol: {
//     span: 16,
//   },
// };
const tailLayout = {
  wrapperCol: {
    offset: 8,
    span: 16,
  },
};

export default function Login({ history }) {
  const [loading, setLoading] = React.useState(false);
  const onFinish = async (values) => {
    console.log("Success:", values);
    setLoading(true);
    try {
      const login = await AccountRepository.login(
        values.username,
        values.password
      );
      console.log(login);
      history.push("/");
    } catch (error) {
      console.log(error);
    } finally {
      setLoading(false);
    }
  };

  const onFinishFailed = (errorInfo) => {
    console.log("Failed:", errorInfo);
  };
  return (
    <Row
      type="flex"
      justify="center"
      align="middle"
      style={{ minHeight: "100vh" }}
    >
      {loading ? (
        <Spin />
      ) : (
        <div>
          <Title style={{ marginLeft: 90 }} level={1}>
            Admin Panel
          </Title>
          <br />
          <Title style={{ marginLeft: 100 }} level={3}>
            Login to continue
          </Title>
          <br />
          <Form
            // {...layout}
            name="basic"
            initialValues={{
              remember: true,
            }}
            onFinish={onFinish}
            onFinishFailed={onFinishFailed}
          >
            <Form.Item
              label="Username"
              name="username"
              rules={[
                {
                  required: true,
                  message: "Please input your username!",
                },
              ]}
            >
              <Input />
            </Form.Item>

            <Form.Item
              label="Password"
              name="password"
              rules={[
                {
                  required: true,
                  message: "Please input your password!",
                },
              ]}
            >
              <center>
                <Input.Password />
              </center>
            </Form.Item>

            <Form.Item {...tailLayout} name="remember" valuePropName="checked">
              <center>
                <Checkbox>Remember me</Checkbox>
              </center>
            </Form.Item>

            <Form.Item {...tailLayout}>
              <center>
                <Button type="primary" htmlType="submit">
                  Submit
                </Button>
              </center>
            </Form.Item>
          </Form>
        </div>
      )}
    </Row>
  );
}
