import { notification } from "antd";
import React from "react";
const axios = require("axios");

class ImageUploader extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedFile: null,
      loaded: null,
      uploaded: false,
    };
    this.onChangeHandler = this.onChangeHandler.bind(this);
    this.uploadImage = this.uploadImage.bind(this);
    this.openNotification = this.openNotification.bind(this);
  }
  //   onFormSubmit() {
  //     console.log("Uplaoding");
  //     const formData = new FormData();
  //     formData.append("myImage", this.state.file);
  //     const config = {
  //       headers: {
  //         "content-type": "multipart/form-data",
  //       },
  //     };
  //     axios
  //       .post("http://localhost:3000/upload", formData, config)
  //       .then((response) => {
  //         console.log(response);
  //         alert("The file is successfully uploaded");
  //       })
  //       .catch((error) => {});
  //   }
  openNotification = () => {
    notification.open({
      message: "Image Uploaded",
      description: "",
      onClick: () => {
        console.log("Notification Clicked!");
      },
    });
  };
  onChangeHandler = (event) => {
    this.setState({
      selectedFile: event.target.files[0],
      loaded: 0,
    });
  };

  uploadImage = () => {
    this.setState({ uploaded: true });
    const data = new FormData();
    data.append("file", this.state.selectedFile);
    axios
      .post(
        "http://ec2-3-21-245-142.us-east-2.compute.amazonaws.com:3000/upload",
        data,
        {
          onUploadProgress: (ProgressEvent) => {
            this.setState({
              loaded: (ProgressEvent.loaded / ProgressEvent.total) * 100,
            });
          },
          // receive two    parameter endpoint url ,form data
        }
      )
      .then((res) => {
        // then print response status
        console.log(res.data.filename);
        this.openNotification();
        this.props.onSuccess(res.data.filename, true);
      })
      .catch((err) => {
        console.log(err);
        this.props.onSuccess(false);
      });
  };

  render() {
    if (this.state.loaded == 100) {
      console.log("Success");
    }
    if (this.state.selectedFile && !this.state.uploaded) {
      this.uploadImage();
    }
    return (
      <input
        type="file"
        name="file"
        style={{ marginTop: 10, marginBottom: 10 }}
        onChange={this.onChangeHandler}
      />
    );
  }
}

export default ImageUploader;
