import { Avatar, Col, Row, Typography, Card, Modal, Button } from "antd";
import Title from "antd/lib/typography/Title";
import React from "react";
import { Redirect } from "react-router-dom";
import CommonLayout from "../../components/CommonLayout";
import { UserOutlined } from "@ant-design/icons";
import { AntDesignOutlined } from "@ant-design/icons";
const { Paragraph } = Typography;

const CustomerView = (props) => {
  const user = props.location.state.user;
  const [visible, setVisible] = React.useState(false);
  const [imageUri, setImageUri] = React.useState(
    "http://ec2-3-21-245-142.us-east-2.compute.amazonaws.com:3000/img/" +
      user.fileName
  );

  console.log(props);
  return (
    <>
      <Modal
        title="Uploaded ID"
        centered
        visible={visible}
        style={{ height: 400, width: 400 }}
        onOk={() => setVisible(false)}
        onCancel={() => setVisible(false)}
      >
        <img src={imageUri} style={{ height: 400, width: 475 }} />
      </Modal>
      <Row style={{ padding: 20 }}>
        <Col span={24}>
          <center>
            <Title level={2}> User Profile</Title>
          </center>
        </Col>
        <Col span={24}>
          <center>
            <Avatar
              size={{ xs: 24, sm: 32, md: 40, lg: 64, xl: 80, xxl: 100 }}
              icon={<AntDesignOutlined />}
            />
            <br />
            <br />
            <Title level={4}>
              {user.firstName} {user.lastName}
            </Title>

            <Paragraph copyable={{ tooltips: false }}>{user.userId}</Paragraph>

            <Paragraph copyable={{ tooltips: false }}>
              {user.customerId}
            </Paragraph>

            <Card style={{ width: 400 }}>
              <Row>
                <Col span={6}>Active Plan : </Col>
                <Col span={18}>
                  <Paragraph copyable={{ tooltips: false }}>
                    {user.plan}
                  </Paragraph>
                </Col>
                <Col span={6}>Email : </Col>
                <Col span={18}>
                  <Paragraph copyable={{ tooltips: false }}>
                    {user.email}
                  </Paragraph>
                </Col>
                <Col span={6}>Permanent Address : </Col>
                <Col span={18}>
                  <Paragraph copyable={{ tooltips: false }}>
                    {user.address}
                  </Paragraph>
                </Col>
                <Col span={6}>Installation Address : </Col>
                <Col span={18}>
                  <Paragraph copyable={{ tooltips: false }}>
                    {user.address}
                  </Paragraph>
                </Col>
                <Col span={6}>Mobile: </Col>
                <Col span={18}>
                  <Paragraph copyable={{ tooltips: false }}>
                    {user.mobile}
                  </Paragraph>
                </Col>
                <Col span={6}>Joined At: </Col>
                <Col span={18}>
                  <Paragraph copyable={{ tooltips: false }}>
                    {user.createdAt}
                  </Paragraph>
                </Col>
                <Col span={24}>
                  <img
                    onClick={() => setVisible(true)}
                    style={{
                      height: 150,
                      width: 150,
                      marginLeft: 20,
                      padding: 10,
                    }}
                    src={imageUri}
                  />
                </Col>
                <Col span={24}>
                  <Button
                    onClick={() =>
                      props.history.push({
                        pathname: "/customer/print",
                        search: "",
                        state: { user: user },
                      })
                    }
                  >
                    Print
                  </Button>
                </Col>
              </Row>
            </Card>
          </center>
        </Col>
      </Row>
    </>
  );
};

export default function ViewCustomer(props) {
  if (!localStorage.getItem("token")) {
    return <Redirect to="login" />;
  }

  return <CommonLayout component={() => <CustomerView {...props} />} />;
}
