import React, { useEffect } from "react";
import {
  IconButton,
  Table,
  TableHead,
  TableBody,
  TableRow,
  TableCell,
  Icon,
  TablePagination,
  CircularProgress,
} from "@material-ui/core";
import "../../App.css";

import { CustomerRepository } from "../../repository/Customer";
import CommonLayout from "../../components/CommonLayout";
import Title from "antd/lib/typography/Title";
import { Button } from "antd";

const CustomerListView = ({ history }) => {
  const [isLoading, setIsLoading] = React.useState(true);
  const [rowsPerPage, setRowsPerPage] = React.useState(5);
  const [userList, setUserList] = React.useState([]);
  const [page, setPage] = React.useState(0);

  const handleMore = (data) => {
    console.log("More Details");
    history.push({
      pathname: "/customer/view",
      search: "",
      state: { user: data },
    });
  };

  useEffect(() => {
    CustomerRepository.getUsers()
      .then((res) => {
        setUserList(res.data);
        setIsLoading(false);
      })
      .catch((err) => {
        console.log(err);
      });
  }, []);

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(+event.target.value);
  };

  if (isLoading) {
    return (
      <div style={{ height: "100vh" }} className="App">
        <CircularProgress style={{ marginTop: 200 }} />
      </div>
    );
  }

  return (
    <div className="w-100 overflow-auto">
      <Title level={2} style={{ margin: 20 }}>
        Customers
      </Title>
      <Table style={{ whiteSpace: "pre" }} style={{ margin: 20 }}>
        <TableHead>
          <TableRow>
            <TableCell className="px-0">customerId</TableCell>
            <TableCell className="px-0">Name</TableCell>
            <TableCell className="px-0">Date</TableCell>
            <TableCell className="px-0">Plan</TableCell>
            <TableCell className="px-0">Action</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {userList
            .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
            .map((subscriber, index) => (
              <TableRow key={index}>
                <TableCell className="px-0 capitalize" align="left">
                  {subscriber.customerId}
                </TableCell>
                <TableCell className="px-0 capitalize" align="left">
                  {subscriber.firstName + " "}
                  {subscriber.lastName}
                </TableCell>
                <TableCell className="px-0 capitalize" align="left">
                  {subscriber.createdAt}
                </TableCell>

                <TableCell className="px-0 capitalize">
                  {subscriber.plan}
                </TableCell>
                <TableCell className="px-0">
                  <Button onClick={() => handleMore(subscriber)}>...</Button>
                </TableCell>
              </TableRow>
            ))}
        </TableBody>
      </Table>
      {userList.length == 0 && (
        <center>
          <h6 style={{ marginTop: 50 }}>No users</h6>
        </center>
      )}

      <TablePagination
        className="px-16"
        rowsPerPageOptions={[5, 10, 25]}
        component="div"
        count={userList.length}
        rowsPerPage={rowsPerPage}
        page={page}
        backIconButtonProps={{
          "aria-label": "Previous Page",
        }}
        nextIconButtonProps={{
          "aria-label": "Next Page",
        }}
        onChangePage={handleChangePage}
        onChangeRowsPerPage={handleChangeRowsPerPage}
      />
    </div>
  );
};

const ViewCustomerList = (props) => {
  return <CommonLayout component={() => <CustomerListView {...props} />} />;
};

export default ViewCustomerList;
