import React, { useEffect } from "react";
import CommonLayout from "../../components/CommonLayout";
import { Row, Col, Button } from "antd";
import Title from "antd/lib/typography/Title";
import { Input, Select, Radio, notification } from "antd";
import { CustomerRepository } from "../../repository/Customer";
import { Redirect } from "react-router-dom";
import ImageUploader from "../../components/ImageUploader";
const { TextArea } = Input;
const { Option } = Select;

const openNotification = () => {
  notification.open({
    message: "User Created Successfully",
    description: "",
    onClick: () => {
      console.log("Notification Clicked!");
    },
  });
};

const CreateCustomerView = (props) => {
  const [uploaded, setUploaded] = React.useState(false);
  const [firstName, setFirstName] = React.useState("");
  const [lastName, setLastName] = React.useState("");
  const [address, setAddress] = React.useState("");
  const [iAddress, setIAddress] = React.useState("");
  const [email, setEmail] = React.useState("");
  const [phone, setPhone] = React.useState("");
  const [idType, setIdType] = React.useState("Aadhar");
  const [idNumber, setIdNumber] = React.useState("");
  const [fileName, setFileName] = React.useState("");
  const [plan, setPlan] = React.useState("");
  const [customerId, setCustomerId] = React.useState("");
  const [userId, setUserId] = React.useState("123");
  const [area, setArea] = React.useState("jlp");
  const onChange = (e) => {
    console.log(`radio checked:${e.target.value}`);
    setPlan(e.target.value);
  };
  const handleImageUpload = (filename, result) => {
    console.log(result);
    setFileName(filename);
    setUploaded(result);
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (!uploaded) {
      console.log("still uploading");
    } else {
      const data = {
        area,
        firstName,
        lastName,
        email,
        address,
        iAddress,
        mobile: phone,
        idType,
        idNumber,
        fileName,
        plan,
        customerId,
        userId,
      };
      console.log(data);
      try {
        const res = await CustomerRepository.createUser(data);
        if (res.responseCode == "SUCCESS") {
          openNotification();
          props.history.push({
            pathname: "/customer/view",
            state: { user: res.data },
          });
          console.log(res);
        }
      } catch (error) {
        console.log(error);
      }
    }
  };

  useEffect(() => {
    CustomerRepository.getCustomerId()
      .then((res) => {
        setCustomerId("MXI000" + res.customerId);
      })
      .catch((err) => {
        console.log(err);
      });
  }, []);
  return (
    <>
      <Row style={{ padding: 20, width: "60vw" }}>
        <Col span={24}>
          <Title level={2}>Create Customer</Title>
        </Col>
      </Row>
      <Row style={{ padding: 20 }}>
        <Col span={24}>
          <Title level={5}>Select Area</Title>
          <Radio.Group
            onChange={onChange}
            defaultValue="499"
            size="large"
            onChange={(e) => setArea(e.target.value)}
          >
            <Radio.Button value="jlp">Jalalpur</Radio.Button>
            <Radio.Button value="akb">Akbarpur</Radio.Button>
            <Radio.Button value="tnd">Tanda</Radio.Button>
            <Radio.Button value="bsk">Baskhari</Radio.Button>
          </Radio.Group>
        </Col>
        <br />
      </Row>
      <Row style={{ padding: 20 }}>
        <Col span={6}>
          <Title level={5}>Firstname</Title>
          <Input size="medium" onChange={(e) => setFirstName(e.target.value)} />
        </Col>
        <Col span={1} />
        <Col span={6}>
          <Title level={5}>Lastname</Title>
          <Input size="medium" onChange={(e) => setLastName(e.target.value)} />
        </Col>
        <Col span={1} />
        <Col span={6} style={{ marginLeft: 10 }}>
          <Title level={5}>Email</Title>
          <Input size="medium" onChange={(e) => setEmail(e.target.value)} />
        </Col>
      </Row>
      <Row style={{ paddingLeft: 20 }}>
        <Col span={6}>
          <Title level={5}>Phone</Title>
          <Input size="medium" onChange={(e) => setPhone(e.target.value)} />
        </Col>
        <Col span={1} />
        <Col span={6}>
          <Title level={5}>ID proof</Title>
          <Input.Group compact>
            <Select
              value={idType}
              defaultValue="Aadhar"
              onSelect={(e) => setIdType(e)}
            >
              <Option value="Aadhar">Aadhar</Option>
              <Option value="VoterID">VoterID</Option>
            </Select>
            <Input
              size="medium"
              style={{ width: "65%" }}
              onChange={(e) => setIdNumber(e.target.value)}
            />
          </Input.Group>
        </Col>
        <Col span={1} />
        <Col span={6}>
          <Title level={5}>Select ID</Title>
          <ImageUploader onSuccess={handleImageUpload} />
        </Col>
      </Row>
      <Row style={{ marginTop: 10, paddingLeft: 20 }}>
        <Col span={10}>
          <Title level={5}>Select Plan</Title>
          <Radio.Group
            onChange={onChange}
            defaultValue="499"
            size="large"
            onChange={(e) => setPlan(e.target.value)}
          >
            <Radio.Button value="499">Basic</Radio.Button>
            <Radio.Button value="799">Silver</Radio.Button>
            <Radio.Button value="1299">Gold</Radio.Button>
            <Radio.Button value="2299">Diamond</Radio.Button>
          </Radio.Group>
        </Col>
      </Row>
      <Row style={{ paddingLeft: 20, marginTop: 20 }}>
        <Col span={20}>
          <Title level={5}>Permanent Address</Title>
          <TextArea
            showCount
            maxLength={100}
            onChange={(e) => setAddress(e.target.value)}
          />
        </Col>

        <Col span={20}>
          <Title level={5}>Installation Address</Title>
          <TextArea
            showCount
            maxLength={100}
            onChange={(e) => setIAddress(e.target.value)}
          />
        </Col>
      </Row>
      <Row style={{ paddingLeft: 20 }}>
        <Col span={2}>
          <Button type="primary" size="large" onClick={handleSubmit}>
            Create
          </Button>
        </Col>
        <Col span={2}>
          <Button type="primary" size="large" danger>
            Reset
          </Button>
        </Col>
      </Row>
      <br />
    </>
  );
};
export default function CreateCutomer(props) {
  if (!localStorage.getItem("token")) {
    return <Redirect to="login" />;
  }
  return <CommonLayout component={() => <CreateCustomerView {...props} />} />;
}
