import serviceCaller from "../api/serviceCaller";

class Repository {
  async createUser(user) {
    const response = await serviceCaller("customers", "create", {
      ...user,
    });
    console.log("User Created", response);
    return response;
  }

  async getUsers() {
    const response = await serviceCaller("customers", "getAll", {});
    console.log(response);
    return response;
  }

  async getCustomerId() {
    const response = await serviceCaller("customers", "getCustomerId", {});
    return response;
  }
}

export const CustomerRepository = new Repository();
