import "./App.css";
import { useState } from "react";
import { Route, Switch, BrowserRouter as Router } from "react-router-dom";
import Login from "./screens/auth/login/Login";
import Signup from "./screens/auth/signup/Signup";
import Home from "./screens/home/Home";
import NotFound from "./screens/NotFound";
import CreateCutomer from "./screens/customers/CreateCutomer";
import ViewCustomer from "./screens/customers/ViewCustomer";
import ViewCustomerList from "./screens/customers/ViewCustomerList";
import PrintUser from "./screens/customers/PrintUser";

function App() {
  const [user, setUser] = useState("");
  return (
    <Router>
      <Switch>
        <Route exact path="/" component={Home} />
        <Route path="/login" component={Login} />
        <Route path="/register" component={Signup} />
        <Route path="/createUser" component={CreateCutomer} />
        <Route path="/viewUser" component={ViewCustomerList} />
        <Route path="/customer/view" component={ViewCustomer} />
        <Route path="/customer/print" component={PrintUser} />
        <Route component={NotFound} />
      </Switch>
    </Router>
  );
}

export default App;
