import endpoints from "./endpoints";
import { ResponseError } from "./error";
// const HOST = "http://localhost:3000";
const HOST = "http://ec2-3-21-245-142.us-east-2.compute.amazonaws.com:3000";

let token;

export default async function serviceCaller(service, api, body, query, params) {
  // token = await DB.retrieveData("token");
  token =
    "eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJhZG1pbiI6eyJpZCI6IjVmYTk2ZTY2Yzg1ZmE1M2JjMGEzZDcxZiJ9LCJpYXQiOjE2MDUwOTMxMDB9.DYRXqVdyvmN3HFrNxa5yDYgqZb5ktxFY_Ti6QY1AJCE";
  const request = endpoints[service][api];
  const option = {
    method: request.method,
    headers: {
      "Content-Type": "application/json",
      token: token,
    },
  };
  console.log("attaching body", body, request);
  if (request.method !== "get" && body) {
    option.body = JSON.stringify(body);
  }
  let url = `${HOST}${request.path}`;
  if (params) {
    params.forEach((param) => {
      url = url.replace(`:${param.key}`, param.value);
    });
  }
  url = new URL(url);

  if (query) {
    const searchParams = new URLSearchParams(query).toString();
    url = `${url.toString()}?${searchParams}`;
  }
  const apiIdentity = `${option.method} ${url}`;
  console.log(`==>${apiIdentity}`);
  try {
    const response = await fetch(url, option);
    const jsonResponse = await response.json();
    if (jsonResponse.type === "error") throw new ResponseError(jsonResponse);
    return jsonResponse;
  } catch (err) {
    throw err instanceof ResponseError ? err : new ResponseError(err);
  }
}
