import React from "react";
import { Redirect } from "react-router-dom";
import CommonLayout from "../../components/CommonLayout";
import { Statistic, Row, Col, Button } from "antd";
import { CustomerRepository } from "../../repository/Customer";

const DashboardView = (props) => {
  const [totalUser, setTotalUser] = React.useState(0);
  const handleViewUser = () => {
    props.history.push("/viewUser");
  };
  React.useEffect(() => {
    CustomerRepository.getCustomerId()
      .then((res) => {
        setTotalUser(res.totalUser);
      })
      .catch((err) => {
        console.log(err);
      });
  }, []);
  return (
    <div>
      <center>
        <h1 style={{ fontSize: 30, marginTop: 10 }}>Weclome To Dashboard</h1>
        <Row
          gutter={16}
          style={{ backgroundColor: "#fff", padding: 20, margin: 50 }}
        >
          <Col span={12}>
            <Statistic title="Active Users" value={totalUser} />
            <Button
              style={{ marginTop: 16 }}
              type="primary"
              onClick={handleViewUser}
            >
              View
            </Button>
          </Col>
          <Col span={12}>
            <Statistic title="Due Payments" value={112893} precision={2} />
            <Button style={{ marginTop: 16 }} type="primary">
              View
            </Button>
          </Col>
        </Row>
      </center>
    </div>
  );
};

export default function Home(props) {
  if (!localStorage.getItem("token")) {
    return <Redirect to="login" />;
  }
  return (
    <div>
      <CommonLayout component={() => <DashboardView {...props} />} />
    </div>
  );
}
